.skills-container {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
}

.skills-sub-container {
    width: 1000px;
    display: flex;
    flex-direction: column;
    gap: 100px;
}

.skills-section,
.technical-skills,
.language-skills {
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
    justify-content: center;
}

.skill-item-img {
    width: 50px;
    height: 50px;
}

h3 {
    margin: 50px 0;
    opacity: 0.6;
    text-transform: uppercase;
}

.skill-summary-item, 
.skill-category,
.language-item {
    width: 270px;
    padding: 25px 15px;
}

.technical-skill-content {
    padding: 15px;
}

.lanaguage-level {
    font-weight: 300;
}

.skill-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.skill-name {
    flex: 1;
    text-align: left;
    margin-right: 10px; 
    font-size: 12.5px;
}

.skill-bar {
    flex: 2;
    background-color: var(--highlight-gray);
    border-radius: 10px;
    display: flex;
    height: 8px;
}

.skill-progress {
    background-color: var(--tertiary-colour);
    border-radius: 10px;
    height: 100%;
}

h2 {
    margin-bottom: 10px;
}

@media (max-width: 1100px) {
    .skills-sub-container {
        width: 800px;
    }
}


@media (max-width: 900px) {
    .skills-sub-container {
        width: 500px;
        gap: 50px;
    }
}

