.photo-gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 0;
  justify-content: center;
  padding: 0 120px;
}

.photo-item {
  position: relative;
  width: 250px;
  height: 250px;
  margin: 0;
}

.photo-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--photo-overlay);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.3s ease;
}

.photo-item:hover .photo-overlay {
  opacity: 1;
  cursor: pointer;
}

.photo-caption {
  color: var(--secondary-colour);
  text-align: center;
}

.photo-caption h4,
.photo-caption p {
  margin: 0;
}

@media (max-width: 768px) {
  .photo-gallery {
    padding: 0 20px;
  }  

  .photo-item {
    width: 200px;
    height: 200px;
  }
  
}

@media (max-width: 440px) {
  .photo-gallery {
    padding: 0 10px;
  }  
  .photo-item {
    width: 150px;
    height: 150px;
  }
}